import type { z } from 'zod'
import { faker as f } from '@faker-js/faker'

import { format } from 'date-fns'
import type { Departure } from '@/shared/types'
import { normalizeDeparture } from './normalizers'
import type { DepartureSchema } from '../schemas'

export function makeDepartureSchemaMock(): z.infer<typeof DepartureSchema> {
  return {
    id: f.number.int() as Departure['id'],
    time: format(f.date.future(), 'HH:ii'),
    durationSeconds: f.number.int({ min: 60 * 60, max: 60 * 60 * 3 }),
  }
}

export const makeDepartureMock = (): Departure => normalizeDeparture(makeDepartureSchemaMock())
